import '@telerik/kendo-theme-default/styles/packages/dropdowns';
import { Component, Input, Output, EventEmitter, ViewChildren, QueryList, ElementRef, ViewChild } from '@angular/core';
import { DropDownsUtil as Util } from '@telerik/kendo-dropdowns-common';
import { ListItem } from './list-item.directive';
import { ItemTemplateDirective } from './templates/item-template.directive';
import { isPresent } from './util';
import { SelectionService } from './selection.service';
import { Subscription } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/merge';

/**
 * @hidden
 */
@Component({
    selector: 'kendo-list',
    template: `
    <div #content [ngClass]="setContainerClasses()" [style.maxHeight]="getHeight()" unselectable="on">
    <ul role="listbox" [attr.id]="id" [attr.aria-hidden]="!show" [ngClass]="{ 'k-list': true, 'k-reset': true }">
        <li role="option"
            *ngFor="let dataItem of data; let index = index"
            kendoDropDownsSelectable
            [index]="index"
            [attr.id]="optionPrefix + '-' + getValue(dataItem)"
            [attr.tabIndex]="-1"
            [ngClass]="{'k-item': true}">
            <template *ngIf="template"
                [templateContext]="{
                    templateRef: template.templateRef,
                    $implicit: dataItem
                }">
            </template>
            <template [ngIf]="!template">{{ getText(dataItem) }}</template>
        </li>
    </ul>
    </div>
  `
})

export class ListComponent {
    @Input() public data: any[] = [];
    @Input() public selected: any[] = [];
    @Input() public focused: number = -1;
    @Input() public textField: string;
    @Input() public valueField: string;
    @Input() public height: number;
    @Input() public template: ItemTemplateDirective;
    @Input() public show: boolean;
    @Input() public id: string;
    @Input() public optionPrefix: string;

    @Output() public onClick: EventEmitter<any> = new EventEmitter();

    @ViewChildren(ListItem) public items: QueryList<ListItem>;
    @ViewChild('content') public content: ElementRef;

    private selectionService: SelectionService;
    private scrollSubscription: Subscription;

    constructor (selectionService: SelectionService) {
        this.selectionService = selectionService;
        this.scrollSubscription = this.selectionService
                                        .onSelect
                                        .map(args => args.indices[0])
                                        .merge(this.selectionService.onFocus)
                                        .subscribe(this.scrollToItem.bind(this));
    }

    public ngAfterViewInit(): void {
        if (this.show === true) {
            this.scrollToItem(this.selectionService.focused);
        }
    }

    public ngOnDestroy(): void {
        this.scrollSubscription.unsubscribe();
    }

    public setContainerClasses(): any {
        return {
            'k-list-scroller': true
        };
    }

    public getHeight(): string {
        return this.height + "px";
    }

    public getText(dataItem: any): any {
        return Util.getter(dataItem, this.textField);
    }

    public getValue(dataItem: any): any {
        return Util.getter(dataItem, this.valueField);
    }

    public scrollToItem(index: number): void {
        const items: Array<any> = this.items.toArray();
        if (isPresent(items[index]) && index !== -1) {
            this.scroll(items[index].element);
        }
    }

    public scroll(item: ElementRef): void {
        if (!item) {
            return;
        }

        const nativeElement: HTMLElement = item.nativeElement;

        let content: any = this.content.nativeElement,
            itemOffsetTop: number = nativeElement.offsetTop,
            itemOffsetHeight: number = nativeElement.offsetHeight,
            contentScrollTop: number = content.scrollTop,
            contentOffsetHeight: number = content.clientHeight,
            bottomDistance: number = itemOffsetTop + itemOffsetHeight;

        if (contentScrollTop > itemOffsetTop) {
            contentScrollTop = itemOffsetTop;
        } else if (bottomDistance > (contentScrollTop + contentOffsetHeight)) {
            contentScrollTop = (bottomDistance - contentOffsetHeight);
        }

        content.scrollTop = contentScrollTop;
    }
}



